#polaroid-gallery {
  position: absolute;
  right: 0;
  width: 30%;
  padding-top: 72px;
  padding-left: 20px;
  top: 0;
  bottom: 0;
  z-index: 1;
  transform-origin: top right;
  &.gallery{
    z-index: 6;
    .polaroid {
      opacity: 0.2;
      &.expanded {
        opacity: 1;
        .close {
          display: block;
        }
      }
    }
  }
  .polaroid {
    padding: 5px;
    padding-bottom: 0;
    box-shadow: rgba(68, 61, 61, 0.5) 1px 1px 9px;
    -webkit-backface-visibility: hidden;
    background: white;
    position: relative;
    .close {
      display: none;
      position: absolute;
      top: -14px;
      right: -13px;
      background-color: #fff;
      height: 40px;
      width: 40px;
      border-radius: 40px;
      font-size: 17px;
      box-shadow: 1px 1px 7px;
      text-align: center;
      line-height: 47px;
      cursor: pointer;
    }
    &:hover {
      box-shadow: rgba(68, 61, 61, 0.5) 3px 3px 30px;
      cursor: pointer;
    }
    .image {
      margin-bottom: 20px;
      height: calc(100% - 40px);
      width: calc(100%);
      background-size: cover;
      background-repeat: no-repeat;
    }
    &.expanded {
      position: fixed;
      z-index: 6;
    }
    .description {
      font-size: 0.8em;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      text-align: center;
      position: relative;
      top: -18px;
    }
  }
}
@media screen and (max-width: 620px) {
  #polaroid-gallery {
   position: absolute;
   right: 0;
   width: 33%;
   padding-top: 20px;
   padding-left: 5px;
